import { HashLink } from "react-router-hash-link"

export default function NavHeader() {
    return (
        <>
    <div className="header__logo container">
        <span className="header__logo__logo"></span>
    </div>

    <nav className="header__navbar">
      <div className="container">
        <ul className="header__navlist">
          <HashLink smooth to="/#aboutUs"><li className="nav-link">  </li></HashLink>
          <HashLink smooth to="/#howItsWork"><li className="nav-link">  </li></HashLink>
          <HashLink smooth to="/#feedbacks"><li className="nav-link"></li></HashLink>
          <HashLink smooth to="/#contacts"><li className="nav-link disabled"></li></HashLink>
        </ul>
      </div>
    </nav>
        </>
    )
}